.Footer {
    box-shadow: 0 -3px 6px rgba(0, 0, 0, .13), 0 -3px 6px rgba(0, 0, 0, .1);

    .Footer-container {
        max-width: var(--max-width);
        width: var(--width);
        margin: 0rem auto var(--padding-top-bottom);
        padding-top: 2.25rem;
        display: flex;
        justify-content: space-between;
        gap: 2rem;

        .Footer-container-left,
        .Footer-container-middle,
        .Footer-container-right {
            h3 {
                font-weight: bold;
                margin-bottom: 2rem;
            }
        }

        .Footer-container-left {
            h3 {
                margin-bottom: 0rem;
            }

            p {
                text-align: left;
            }

            img {
                width: 200px;
                height: auto;
                margin-top: 2rem;
            }
        }

        .Footer-container-middle {
            p {
                text-align: left;
                margin-bottom: 0.8rem;
            }
        }

        .Footer-container-right {
            font-size: 1.6rem;

            * {
                display: block;
                margin-bottom: 0.8rem;

                &:hover {
                    background-color: white;
                    padding: 0rem;
                    color: black;
                    text-decoration: underline;
                }
            }
        }

        @media (max-width: 768px) {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
        }

        @media (max-width: 576px) {
            display: flex;
            flex-direction: column;
        }
    }

    .design-bar {
        display: flex;
        justify-content: space-between;
        color: gray;
        letter-spacing: 0.05rem;

        .caption {
            margin: 0rem 0.8rem;
        }
    }
}